.About{
    padding: 3em 0;
}
.profile-image{
    margin: 2em 0;
    border-radius: 100%;
    box-shadow: 0 0 6px 2px #444;
    width: 25%;
    height: auto;
}
@media screen and (max-width: 1024px){
    .profile-image{
        width: 50%;
    }
    .profile-loader{
        width: 50%;
    }
}
@media screen and (max-width: 796px){
    .profile-image{
        width: 70%;
    }
    .profile-loader{
        width: 70%;
    }
}
@media screen and (max-width: 500px){
    .profile-image{
        width: 90%;
    }
    .profile-loader{
        width: 90%;
    }
}
.profile-loader{
    width: 25%;
    border-radius: 100%;
    margin: 2em 0;
    box-shadow: 0 0 6px 2px #444;
    -webkit-animation: loadProfileImage 0.5s;
            animation: loadProfileImage 0.5s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    background-color: #DDD;
}
@-webkit-keyframes loadProfileImage{
    0%{
        background-color: #FFF;
    }
    50%{
        background-color: aliceblue;
    }
    100%{
        background-color: rgb(128, 182, 202);
    }
}
@keyframes loadProfileImage{
    0%{
        background-color: #FFF;
    }
    50%{
        background-color: aliceblue;
    }
    100%{
        background-color: rgb(128, 182, 202);
    }
}
